.sun, .moon {
  font-size: 12px;
}

.moon {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.moon-logo {
  opacity: 0;
  transition: all .25s ease-out;
  transform: translateY(20%)rotateZ(50deg);
}

.sun-logo {
  opacity: 1;
  transition: all .25s ease-out;
  transform: translateY(0)rotateZ(0deg);
}

.animate-sun {
  opacity: 0;
  transform: translateY(0%)rotateZ(100deg);
}

.animate-moon {
  opacity: 1;
  transform: translateY(0%)rotateZ(0deg);
}

.animate-moon path, .animate-sun path {
  fill: #fff;
}

.sun__fill {
  fill: #fce570;
}

.loadingSpinner {
  width: 25px;
  height: 25px;
  display: none;
  position: -webkit-sticky;
  position: sticky;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loadingSpinner .dot {
  width: 18px;
  height: 18px;
  opacity: .5;
  filter: alpha(opacity= 50);
  border-radius: 50%;
  animation: spin 2.5s infinite both;
  position: absolute;
  top: 1px;
  left: 1px;
}

.loadingSpinner .dot2 {
  width: 20px;
  height: 20px;
  opacity: .75;
  filter: alpha(opacity= 50);
  background-color: #ff001b;
  border-radius: 50%;
  animation: spin2 2.5s infinite both;
  position: absolute;
}

@keyframes spin {
  0%, 100% {
    box-shadow: 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b;
  }

  50% {
    transform: rotate(180deg);
  }

  25%, 75% {
    box-shadow: 28px 0 #ff001b, -28px 0 #ff001b, 0 28px #ff001b, 0 -28px #ff001b, 20px -20px #ff001b, 20px 20px #ff001b, -20px -20px #ff001b, -20px 20px #ff001b;
  }

  100% {
    transform: rotate(360deg);
    box-shadow: 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b, 0 0 #ff001b;
  }
}

@keyframes spin2 {
  0%, 100% {
    box-shadow: 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08;
  }

  50% {
    transform: rotate(-180deg);
  }

  25%, 75% {
    background: none;
    box-shadow: 52px 0 #ff6b08, -52px 0 #ff6b08, 0 52px #ff6b08, 0 -52px #ff6b08, 38px -38px #ff6b08, 38px 38px #ff6b08, -38px -38px #ff6b08, -38px 38px #ff6b08;
  }

  100% {
    transform: rotate(-360deg);
    box-shadow: 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08, 0 0 #ff6b08;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 1;
  background-color: rgba(47, 48, 58, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop.is-hidden .backdrop__modal {
  transform: translate(-50%, -50%)scale(.9);
}

